<template>
    <div class="ticket">
      <p class="centered" style="font-size: 20px; width:125%;">   </p>
      <hr style="margin-top: 0; width:125%;">
      <table style="border: none;">
        <thead>
        <th class="text-center" style="border: none;"> . : <span> {{ invoice_id }} </span> </th>
        <th class="text-center" style="border: none;"> . : <span> {{ table_num }} </span> </th>
        <th class="text-center" style="border: none;">  : <span> {{ type | type_filter }} </span> </th>
        </thead>
      </table>
      <br>
      <table style="border: none;">
        <thead>
        <th class="text-center" style="border: none;">  : <span> {{ current_date }} </span> </th>
        <th class="text-center" style="border: none;">  : <span dir="ltr"> {{ get_current_time }} </span> </th>
        </thead>
      </table>
      <br>
      <table class="text-center">
        <thead>
          <tr>
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"> </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="food in foods" :key="food.id">
            <td> {{ food.food_name }} </td>
            <td> {{ food.price }} </td>
            <td> {{ food.qty }} </td>
            <td> {{ food.all_price }} </td>
          </tr>
        </tbody>
      </table>
      <br>
      <table style="border: none;">
        <thead>
        <th class="text-center" style="border: none; width: 50%;">  : <span> {{ discount }} </span> IQD  </th>
        <th class="text-center" style="border: none; width: 50%;">  : <span> {{ service }} </span> IQD </th>
        </thead>
      </table>
      <br>
      <table style="border: none;">
        <thead>
        <th class="text-center" style="border: none; width: 50%;">   : <span> {{ total_price | money_filter }} </span> IQD  </th>
        <th class="text-center" style="border: none; width: 50%;">   : <span> {{ (total_price / dollar_price).toFixed(2) }} </span> $ </th>
        </thead>
      </table>
      <br>
      <table style="border: none;">
        <thead>
        <th class="text-center" style="border: none; width: 50%;">   : <span> {{ amount_paid | money_filter }} </span> IQD  </th>
        <th class="text-center" style="border: none; width: 50%;">   : <span> {{ (amount_paid  - total_price) | money_filter }} </span> IQD </th>
        </thead>
      </table>
      <br>
      <table v-if="type == 2">
        <thead>
        <th class="text-center py-2" colspan="2">   : <span> {{ delivery_info.address }} </span>  </th>
        </thead>
        <thead>
        <th class="text-center py-2">   : <span> <br> {{ delivery_info.phone }} </span>  </th>
        <th class="text-center py-2">   : <span> <br> {{ delivery_info.price | money_filter }} </span> IQD  </th>
        </thead>
      </table>
      <hr style="width:125%;">
      <p class="centered" style="width:125%;">     </p>
    </div>
</template>
<script>
    import axios from "axios";
    var d = new Date();
    export default {
        data(){
            return {
                current_date:  d.toISOString().split("T")[0],
                invoice_id: 0,
                total_price: 0,
                amount_paid: 0,
                service: 0,
                discount: 0,
                dollar_price: 0,
                table_num: 0,
                type: 0,
                foods: [],
                delivery_info: {
                    address: "",
                    phone: "",
                    price: ""
                }
            }
        },
        created() {
          this.invoice_id = this.$route.params.invoice_id || 0;
          axios.post("http://localhost:3000/dollar/getData").then(({data}) => {
            this.dollar_price = data.dollar_price;
          });
          if(this.invoice_id != 0){
            axios.post("http://localhost:3000/invoice/getInvoiceDetail/" + this.invoice_id).then(({data}) => {
              this.table_num = data.invoice_detail.table_num || null;
              this.type = data.invoice_detail.type;
              this.current_date = data.invoice_detail.invoice_date.slice(0, 10);
              this.total_price = data.invoice_detail.total_price;
              this.amount_paid = data.invoice_detail.amount_paid;
              this.discount = data.invoice_detail.discount;
              this.service = data.invoice_detail.service;

              this.foods = data.foods;

              this.delivery_info.address = data.delivery_info.address;
              this.delivery_info.phone = data.delivery_info.phone;
              this.delivery_info.price = data.delivery_info.delivery_price;
            });
          }
        },
        filters: {
            money_filter: function(value){
                var arr = value.toString().split("").reverse();
                var arr2 = [];
                for(let i=0; i < arr.length; i++){
                    arr2.unshift(arr[i]);
                    if((i + 1) % 3 == 0){
                        arr2.unshift(",");
                    }
                }
                var number_with_comma = arr2.join("");
                if(number_with_comma.charAt(0) == ','){
                    return number_with_comma.slice(1);
                }
                return number_with_comma;
            },
            type_filter: function(value){
              if(value == 0){
                return "";
              } else if(value == 1){
                return "";
              }
              return "";
            }
        },
        computed: {
            get_current_time: function(){
                var hour = d.getHours();
                if(hour >= 12){
                    return hour - 12 + " : " + d.getMinutes() + " : " + d.getSeconds() + " PM";
                } 
                return hour + " : " + d.getMinutes() + " : " + d.getSeconds() + " AM";
            }
        }
    }
</script>

<style scoped>
    * {
        font-size: 16px;
        color: black;
    }
    table{
        width: 380px;
    }
    td,
    th,
    tr,
    table {
        border: 1px solid black;
        border-collapse: collapse;
    }

    td.description,
    th.description {
        width: 75px;
        max-width: 75px;
    }

    td.quantity,
    th.quantity {
        width: 40px;
        max-width: 40px;
        word-break: break-all;
    }

    td.price,
    th.price {
        width: 40px;
        max-width: 40px;
        word-break: break-all;
    }

    .centered {
        margin-top: 10px;
        text-align: center;
        align-content: center;
        margin-bottom: 10px;
    }

    .ticket {
        width: 302px;
        max-width: 302px;
    }

    @media print {
      @page {
        size: 80mm 297mm !important;
        margin-top: 0 !important;
        margin-bottom: 5mm !important;
        margin-left: 0 !important;
        margin-right: 5.8mm !important;
      }
    }
</style>