<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight"></app-nav>
        <div id="content-wrapper" style="display:none;" class="animate__animated animate__fadeInDown">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <h3 class="text-dark mb-0"> </h3>
                    <header-btns></header-btns>
                </div>
                <hr>
                <div class="row">
                    <div class="col-md-4 col-xl-3 mb-4">
                        <div class="card shadow border-left-success py-2">
                            <div class="card-body">
                                <div class="row align-items-center no-gutters">
                                    <div class="col mr-2 text-right">
                                        <div class="text-primary font-weight-bold text-md mb-2"><span> </span></div>
                                        <div class="text-dark font-weight-bold h4 mb-0"> <span>{{ daily_sold }}</span> IQD </div>
                                    </div>
                                    <div>
                                        <i class="fas fa-cash-register fa-2x"></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-4 col-xl-3 mb-4">
                        <div class="card shadow border-left-primary py-2">
                            <div class="card-body">
                                <div class="row align-items-center no-gutters">
                                    <div class="col mr-2 text-right">
                                        <div class="text-primary font-weight-bold text-md mb-2"><span> </span></div>
                                        <div class="text-dark font-weight-bold h4 mb-0"> <span> {{ no_of_users }} </span> </div>
                                    </div>
                                    <div>
                                        <i class="fas fa-users fa-2x"></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-4 col-xl-3 mb-4">
                        <div class="card shadow border-left-warning py-2">
                            <div class="card-body">
                                <div class="row align-items-center no-gutters">
                                    <div class="col mr-2 text-right">
                                        <div class="text-primary font-weight-bold text-md mb-2"><span> </span></div>
                                        <div class="text-dark font-weight-bold h4 mb-0"> <span> {{ no_of_foods }} </span> </div>
                                    </div>
                                    <div>
                                        <i class="fas fa-pizza-slice fa-2x"></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-4 col-xl-3 mb-4">
                        <div class="card shadow border-left-danger py-2">
                            <div class="card-body">
                                <div class="row align-items-center no-gutters">
                                    <div class="col mr-2 text-right">
                                        <div class="text-primary font-weight-bold text-md mb-2"><span> </span></div>
                                        <div class="text-dark font-weight-bold h4 mb-0"> <span> {{ tables.length }} </span> </div>
                                    </div>
                                    <div>
                                        <i class="fas fa-table fa-2x"></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <hr>
                <div class="row mb-2">
                    <div class="col-xl-3 col-md-4 mb-3"> <span class="btn btn-primary btn-block shadow" data-toggle="modal" data-target="#mdl_food_types">    &nbsp; <i class="fas fa-list"></i> </span> </div>
                    <div class="col-xl-3 col-md-4 mb-3"> <span class="btn btn-primary btn-block shadow" data-toggle="modal" data-target="#mdl_tables" >   &nbsp; <i class="fas fa-table"></i> </span> </div>
                    <div class="col-xl-3 col-md-4 mb-3"> <span class="btn btn-primary btn-block shadow" data-toggle="modal" data-target="#mdl_dollar">    &nbsp; <i class="fas fa-dollar-sign"></i> </span> </div>
                </div>
            </div>
        </div>

        <div class="modal" id="mdl_food_types">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="mt-2 text-dark">   </h5>
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal" v-on:click="clear_food_types_inputs()"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2"> : </label>
                            <input type="number" class="form-control col-sm-8" id="" readonly placeholder="" v-model="single_food_type.type_id">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="text" class="form-control col-sm-8" id="" placeholder="  ..." v-model="single_food_type.type_name">
                        </div>
                        <hr>
                        <div class="row mb-2">
                            <div class="col-sm-4"> <span class="btn btn-success btn-block" v-on:click="add_food_type()">  &nbsp; <i class="fas fa-plus"></i> </span> </div>
                            <div class="col-sm-4"> <span class="btn btn-warning btn-block" v-on:click="update_food_type()">  &nbsp; <i class="fas fa-edit"></i> </span> </div>
                            <div class="col-sm-4"> <span class="btn btn-danger btn-block" v-on:click="delete_food_type()">  &nbsp; <i class="fas fa-trash"></i> </span> </div>
                        </div>
                        <hr>
                        <div class="table-responsive mb-2">
                            <table class="table table-bordered table-striped text-center text-dark">
                                <thead>
                                    <th class="text-center" style="width: 16%;"></th>
                                    <th class="text-center"> </th>
                                    <th class="text-center" style="width: 16%;"></th>
                                </thead>
                                <tbody>
                                    <tr v-for="food_type in food_types" :key="food_type.type_id">
                                        <td>{{ food_type.type_id }}</td>
                                        <td>{{ food_type.type_name }}</td>
                                        <td> <span class="btn btn-primary btn-block" v-on:click="view_food_type(food_type.type_id)">  </span> </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal" id="mdl_tables">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="mt-2 text-dark"></h5>
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal" v-on:click="clear_table_inputs()"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2"> : </label>
                            <input type="number" class="form-control col-sm-8" id="" min="1" readonly placeholder="" v-model="single_table.table_id">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="number" class="form-control col-sm-8" id="" min="1"  placeholder="  ..." v-model="single_table.table_num">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <select id="" class="form-control col-sm-8" v-model="single_table.position">
                                <option value="Inside">  </option>
                                <option value="Outside">  </option>
                            </select>
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <select id="" class="form-control col-sm-8" v-model="single_table.type">
                                <option value="Normal">  </option>
                                <option value="VIP"> VIP </option>
                            </select>
                        </div>
                        <hr>
                        <div class="row mb-2">
                            <div class="col-sm-4"> <span class="btn btn-success btn-block" v-on:click="add_table()">  &nbsp; <i class="fas fa-plus"></i> </span> </div>
                            <div class="col-sm-4"> <span class="btn btn-warning btn-block" v-on:click="update_table()">  &nbsp; <i class="fas fa-edit"></i> </span> </div>
                            <div class="col-sm-4"> <span class="btn btn-danger btn-block" v-on:click="delete_table()">  &nbsp; <i class="fas fa-trash"></i> </span> </div>
                        </div>
                        <div class="table-responsive mb-2">
                            <table class="table table-bordered table-striped text-center text-dark">
                                <thead>
                                    <th class="text-center" style="width: 15%;"></th>
                                    <th class="text-center" style="width: 20%;"> </th>
                                    <th class="text-center"> </th>
                                    <th class="text-center"> </th>
                                    <th class="text-center"> </th>
                                    <th class="text-center" style="width: 10%;"></th>
                                </thead>
                                <tbody>
                                    <tr v-for="table in tables" :key="table.table_id">
                                        <td> {{ table.table_id }} </td>
                                        <td> {{ table.table_num }} </td>
                                        <td> {{ table.position | table_position_filter }} </td>
                                        <td> {{ table.type | table_type_filter }} </td>
                                        <td> {{ table.state | table_state_filter }} </td>
                                        <td> <span class="btn btn-primary btn-block" v-on:click="view_table(table.table_id)"> <i class="fas fa-edit"></i> </span> </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal" id="mdl_dollar">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="mt-2 text-dark"> </h5>
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="row mb-2 justify-content-center">
                            <div class="alert alert-primary col-sm-11 text-right" >
                                  : &nbsp; <span dir="rtl"> {{ dollar.last_update_value.slice(0,10) }} </span>
                            </div>  
                            <label class="col-sm-3 mt-2"> 1  = </label>
                            <input type="number" id="" class="form-control col-sm-8" placeholder="IQD" v-model="dollar.dollar_price">
                        </div>
                        <div class="row mb-2">
                            <div class="col-sm-12"><span class="btn btn-warning btn-block" v-on:click="update_dollar_price()">   <i class="fas fa-edit"></i></span></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import showError from "../showError.js";
    import auth from "../auth.js";

    axios.defaults.withCredentials = true;

    const nullValues = [null,""];

    export default {
        name:"index",
        data() {
            return {
                daily_sold: 0,
                no_of_users: 0, 
                no_of_foods: 0, 
                food_types: [],
                single_food_type: {
                    type_id: null,
                    type_name: null
                },
                tables: [],
                single_table: {
                    table_id: null,
                    table_num: null,
                    position: "Inside",
                    state: "0",
                    type: "Normal"
                },
                dollar:{
                    dollar_price: 0,
                    last_update_value: ""
                }
            }
        },
        methods:{
            view_food_type: function(type_id){
                const [filteredType] = this.food_types.filter(obj => obj.type_id === type_id);
                this.single_food_type.type_id = filteredType.type_id;
                this.single_food_type.type_name = filteredType.type_name;
            },
            view_table: function(table_id){
                const [filteredTable] = this.tables.filter(obj => obj.table_id === table_id);
                this.single_table.table_id = filteredTable.table_id;
                this.single_table.table_num = filteredTable.table_num;
                this.single_table.position = filteredTable.position;
                this.single_table.state = filteredTable.state;
                this.single_table.type = filteredTable.type;
            },
            clear_table_inputs: function(){
                this.single_table.table_id = null;
                this.single_table.table_num = null;
                this.single_table.position = "Inside";
                this.single_table.state = "0";
                this.single_table.type = "Normal";
            },
            clear_food_types_inputs: function(){
                this.single_food_type.type_id = null;
                this.single_food_type.type_name = null;
            },
            add_food_type: function(){
                if(!nullValues.includes(this.single_food_type.type_name)){
                    axios.post("http://localhost:3000/food_type/addFoodType",{
                        type_name: this.single_food_type.type_name
                    }).then(({data}) => {
                        this.food_types.push({
                            type_id: data.type_id,
                            type_name: this.single_food_type.type_name
                        });
                        this.clear_food_types_inputs();
                    }).catch((err)=>{
                        showError(err);
                    });
                } else {
                    alert("   ");
                }
            },
            update_food_type: function(){
                if(!nullValues.includes(this.single_food_type.type_id) && !isNaN(this.single_food_type.type_id)){
                    if(!nullValues.includes(this.single_food_type.type_name)){
                        axios.patch("http://localhost:3000/food_type/updateFoodType/"+this.single_food_type.type_id,{
                            type_name: this.single_food_type.type_name
                        }).then(()=>{
                            let index = this.food_types.findIndex(obj => obj.type_id === this.single_food_type.type_id);
                            this.food_types[index].type_name = this.single_food_type.type_name;
                            this.clear_food_types_inputs();
                        }).catch((err)=>{
                            showError(err);
                        });
                    } else {
                        alert("   ");
                    }
                } else {
                    alert("      ");
                }
            },
            delete_food_type: function(){
                if(!nullValues.includes(this.single_food_type.type_id && !isNaN(this.single_food_type.type_id))){
                    const c = confirm("     ");
                    if(c){
                        axios.delete("http://localhost:3000/food_type/deleteFoodType/"+this.single_food_type.type_id).then(()=>{
                            this.food_types = this.food_types.filter(obj => obj.type_id !== this.single_food_type.type_id);
                            this.clear_food_types_inputs();
                        }).catch((err) => {
                            showError(err);
                        });
                    }
                } else {
                    alert("      ");
                }
            },
            add_table: function(){
                if(!nullValues.includes(this.single_table.table_num) && !isNaN(this.single_table.table_num) && ["Inside","Outside"].includes(this.single_table.position)  && ["Normal","VIP"].includes(this.single_table.type)){
                    axios.post("http://localhost:3000/tables/addTable",{
                        table_num: this.single_table.table_num,
                        position: this.single_table.position,
                        type: this.single_table.type
                    }).then(({data}) => {
                        this.tables.push({
                            table_id: data.table_id,
                            table_num: this.single_table.table_num,
                            position: this.single_table.position,
                            state: this.single_table.state,
                            type: this.single_table.type
                        });
                        this.clear_table_inputs();
                    }).catch((err) => {
                        showError(err);
                    });
                } else {
                    alert("     ");
                }
            },
            update_table: function(){
                if(!nullValues.includes(this.single_table.table_id)){
                    if(!nullValues.includes(this.single_table.table_num) && !isNaN(this.single_table.table_num) && ["Inside","Outside"].includes(this.single_table.position)  && ["Normal","VIP"].includes(this.single_table.type)){
                        axios.patch("http://localhost:3000/tables/updateTable/"+this.single_table.table_id,{
                            table_num: this.single_table.table_num,
                            position: this.single_table.position,
                            state: this.single_table.state,
                            type: this.single_table.type
                        }).then(() => {
                            let index = this.tables.findIndex(obj => obj.table_id === this.single_table.table_id);
                            this.tables[index].table_num = this.single_table.table_num;
                            this.tables[index].position = this.single_table.position;
                            this.tables[index].state = this.single_table.state;
                            this.tables[index].type = this.single_table.type;
                            this.clear_table_inputs();
                        }).catch((err) => {
                            showError(err);
                        });
                    } else {
                        alert("     ");
                    }
                } else {
                    alert("      ");
                }
            },
            delete_table: function(){
                if(!nullValues.includes(this.single_table.table_id)){
                    const c = confirm("     ");
                    if(c){
                        axios.delete("http://localhost:3000/tables/deleteTable/"+this.single_table.table_id).then(()=>{
                            this.tables = this.tables.filter(obj => obj.table_id != this.single_table.table_id);
                            this.clear_table_inputs();
                        }).catch((err) => {
                            showError(err);
                        });
                    }
                } else {
                    alert("      ");
                }
            },
            update_dollar_price: function(){
                if(!nullValues.includes(this.dollar.dollar_price)){
                    axios.patch("http://localhost:3000/dollar/updateDollar",{
                        dollar: this.dollar.dollar_price
                    }).then(()=>{
                        this.dollar.last_update_value = new Date().toISOString().split('T')[0];
                        alert("   ");
                    }).catch((err)=>{
                        showError(err);
                    });
                } else {
                    alert("  ");
                }
            }
        },
        created(){
            axios.post("http://localhost:3000/index/getData").then(({data}) => {
                this.dollar.dollar_price = data.dollar_price.dinar;
                this.dollar.last_update_value = data.dollar_price.last_update
                this.daily_sold = data.daily_sold || 0;
                this.no_of_users = data.no_of_users;
                this.no_of_foods = data.no_of_foods;
                this.food_types = data.food_types;
                this.tables = data.tables;
            }).catch((err) => {
                alert(err.message);
            });
        },
        beforeCreate(){
            auth("index");
        },
        filters:{
            table_position_filter(value){
                if(value=="Inside"){
                    return "";
                }
                return "";
            },
            table_type_filter(value){
                if(value=="Normal"){
                    return "";
                }
                return "VIP";
            },
            table_state_filter(value){
                if(value==0){
                    return "";
                } else if(value==1){
                    return "";
                }
                return " ";
            }
            
        }
    }
</script>