<template>
    <div>
        <span class="btn btn-danger shadow mb-2" v-on:click="logout()">  &nbsp; <i class="fas fa-arrow-left"></i> </span>
    </div>
</template>

<script>
    import axios from 'axios';
    
    axios.defaults.withCredentials = true;

    export default {
        name:"Header_btns",
        data(){
            return{
                
            }
        },
        methods: {
            logout: function(){
                axios.post("http://localhost:3000/logout").then(() => {
                    window.location.href = "/login";
                });
            }
        }
    }
</script>