<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight"></app-nav>
        <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <h3 class="text-dark mb-0"></h3>
                    <header-btns></header-btns>
                </div>
                <hr>
                <div class="row mb-2">
                    <div class="col-xl-3 col-md-4"> 
                        <span class="btn btn-success btn-block" data-toggle="modal" data-target="#mdl_user_detail">   &nbsp; <i class="fas fa-plus"></i> </span> 
                    </div>
                    <div class="col-xl-9 col-md-4"> 
                        <div class="float-left">
                            <input type="text" class="form-control text-center" dir="ltr" placeholder="... " v-model="search">
                        </div> 
                    </div>
                </div>
                <hr>
                <div class="table-responsive">
                    <table class="table table-bordered table-striped text-center text-dark">
                        <thead>
                            <th class="text-center"></th>
                            <th class="text-center"> </th>
                            <th class="text-center"> </th>
                            <th class="text-center" style="width: 10%;"></th>
                            <th class="text-center" style="width: 13%;"> </th>
                            <th class="text-center" style="width: 15%;"> </th>
                            <th class="text-center" style="width: 13%;"></th>
                            <th class="text-center" style="width: 6%;"></th>
                            <th class="text-center" style="width: 6%;"></th>
                        </thead>
                        <tbody>
                            <tr v-for="user in searched_users" :key="user.user_id">
                                <td> {{ user.user_id }} </td>
                                <td> {{ user.username }} </td>
                                <td> {{ user.full_name }} </td>
                                <td> {{ user.role }} </td>
                                <td>
                                    <span v-bind:class="{ 'text-danger': user.status == 0, 'text-success': user.status == 1 }"> {{ user.status | user_status_filter }} </span>
                                    <hr>
                                    <span class="btn btn-danger btn-block btn-sm" v-on:click="disable_user(user.user_id)" v-if="user.status == 1"> Disable </span>
                                    <span class="btn btn-success btn-block btn-sm" v-on:click="active_user(user.user_id)" v-else-if="user.status == 0"> Active </span>
                                </td>
                                <td> {{ user.reg_date.slice(0,10) }} </td>
                                <td class="phone"> {{ user.phone }} </td>
                                <td> <span class="btn btn-primary btn-block" v-on:click="get_single_user(user.user_id)" data-toggle="modal" data-target="#mdl_user_detail"> <i class="fas fa-edit"></i> </span> </td>
                                <td> <span class="btn btn-danger btn-block" v-on:click="del_user(user.user_id)"> <i class="fas fa-trash"></i> </span> </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                
            </div>
        </div>
        <div class="modal text-dark" id="mdl_user_detail">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="mt-2"> </h5>
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal" v-on:click="clear_single_user_inputs()"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2"> : </label>
                            <input type="number" readonly class="form-control col-sm-8" v-model="single_user.user_id">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="text"   required class="form-control col-sm-8" placeholder="    ..." v-model="single_user.username">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="password"    class="form-control col-sm-8" placeholder="    ..." v-model="single_user.password">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="text"   required class="form-control col-sm-8" placeholder="     ..." v-model="single_user.full_name">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">: </label>
                            <select  class="form-control col-sm-8" v-model="single_user.role">
                                <option value="User">User</option>
                                <option value="Admin">Admin</option>
                            </select>
                        </div>
                        <hr>
                        <div class="row mb-2 border-1" v-if="single_user.role == 'User'">
                            <label class="col-sm-3 mt-2">: </label>
                            
                            <div class="form-check col-sm-3">
                                <input type="checkbox" class="form-check-input"  value="foods" v-model="single_user.roles">
                                &nbsp;&nbsp;&nbsp;
                                <label class="form-check-label mr-3" ></label>
                            </div>
                            <div class="form-check col-sm-3">
                                <input type="checkbox" class="form-check-input"  value="cashier" v-model="single_user.roles">
                                &nbsp;&nbsp;&nbsp;
                                <label class="form-check-label mr-3" ></label>
                            </div>
                            <div class="form-check col-sm-3">
                                <input type="checkbox" class="form-check-input"  value="tables" v-model="single_user.roles">
                                &nbsp;&nbsp;&nbsp;
                                <label class="form-check-label mr-3" ></label>
                            </div>
                            <div class="col-sm-3"></div>
                            <div class="form-check col-sm-3">
                                <input type="checkbox" class="form-check-input"  value="reports" v-model="single_user.roles">
                                &nbsp;&nbsp;&nbsp;
                                <label class="form-check-label mr-3"></label>
                            </div>
                            <div class="form-check col-sm-3">
                                <input type="checkbox" class="form-check-input"  value="expense" v-model="single_user.roles">
                                &nbsp;&nbsp;&nbsp;
                                <label class="form-check-label mr-3" ></label>
                            </div>
                        </div>
                        <hr>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="text" class="form-control col-sm-8" placeholder="    ..."  v-model="single_user.phone">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="file"  class="col-sm-8" @change="processFile($event)">
                        </div>
                        <hr>
                        <div class="row mb-2">
                            <div class="mb-2" v-bind:class="{ 'col-sm-4': single_user.user_id != null, 'col-sm-12': single_user.user_id == null }"> <span class="btn btn-success btn-block" v-on:click="add_user()">   &nbsp; <i class="fas fa-plus"></i> </span> </div>
                            <div class="col-sm-4 mb-2" v-if="single_user.user_id != null"> <span class="btn btn-warning btn-block" v-on:click="update_user()">  &nbsp; <i class="fas fa-edit"></i> </span> </div>
                            <div class="col-sm-4 mb-2" v-if="single_user.user_id != null"> <span class="btn btn-danger btn-block" v-on:click="del_user(single_user.user_id)">  &nbsp; <i class="fas fa-trash"></i> </span> </div>
                            <div class="mb-2" v-bind:class="{ 'col-sm-4': single_user.image_path != null, 'col-sm-6': single_user.image_path == null }" v-if="single_user.user_id != null"> <span class="btn btn-primary btn-block" v-on:click="update_image()">   &nbsp; <i class="fas fa-image"></i> </span> </div>
                            <div class="mb-2" v-bind:class="{ 'col-sm-4': single_user.image_path != null, 'col-sm-6': single_user.image_path == null }" v-if="single_user.user_id != null"> <span class="btn btn-primary btn-block" v-on:click="update_password()">    &nbsp; <i class="fas fa-key"></i> </span> </div>
                            <div class="col-sm-4 mb-2" v-if="single_user.image_path != null"> <span class="btn btn-danger btn-block" v-on:click="del_image()">   &nbsp; <i class="fas fa-trash"></i> </span> </div>
                        </div>
                        <hr>
                        <div class="row mb-2">
                            <img v-bind:src="single_user.image_path" alt="No image" class="col-sm-12 w-100 h-100" v-if="single_user.image_path != null">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import showError from "../showError.js";
    import auth from "../auth.js";

    axios.defaults.withCredentials = true;

    const nullValues = ["", null];
    export default {
        name: "users",
        data(){
            return {
                users: [],
                single_user: {
                    user_id: null,
                    username: null,
                    password: null,
                    full_name: null,
                    role: "User",
                    status: 1,
                    phone: null,
                    image_file: null,
                    image_path: null,
                    roles: []
                },
                search: ""
            }
        },
        beforeCreate(){
            auth("users");
        },
        created(){
            axios.post("http://localhost:3000/user/getData").then(({data}) => {
                this.users = data;
            });
        },
        methods: {
            processFile: function(event){
                this.single_user.image_file = event.target.files[0]
            },
            clear_single_user_inputs: function(){
                this.single_user.user_id = null;
                this.single_user.username = null;
                this.single_user.password = null;
                this.single_user.full_name = null;
                this.single_user.role = "User";
                this.single_user.phone = null;
                this.single_user.image_file = null;
                this.single_user.image_path = null;
                this.single_user.roles = []
            },
            get_single_user: function(user_id){
                const [user] = this.users.filter(obj => obj.user_id == user_id);
                this.single_user.user_id = user.user_id;
                this.single_user.username = user.username;
                this.single_user.full_name = user.full_name;
                this.single_user.role = user.role;
                this.single_user.phone = user.phone;
                this.single_user.image_path = user.image_path;
                if(user.role=="User"){
                    axios.post("http://localhost:3000/user/getUserRoles/" + user.user_id).then(({data})=>{
                        this.single_user.roles = data.roles.split(",");
                    });
                }
            },
            del_user: function(user_id){
                const c = confirm("     ");
                if(c){
                    axios.delete("http://localhost:3000/user/deleteUser/" + user_id).then(()=>{
                        this.users = this.users.filter(obj => obj.user_id != user_id);
                    }).catch((err) => {
                        showError(err);
                    });
                }
            },
            del_image: function(){
                if(!nullValues.includes(this.single_user.user_id)){
                    if(!nullValues.includes(this.single_user.image_path)){
                        const c = confirm("     ");
                        if(c){
                            axios.delete("http://localhost:3000/user/deleteImage/" + this.single_user.user_id).then(() => {
                                let index = this.users.findIndex(obj => obj.user_id === this.single_user.user_id);
                                this.users[index].image_path = null;
                                this.single_user.image_path = null;
                            });
                        }
                    } else {
                        alert("   ");
                    }
                } else {
                    alert("     ");
                }
            },
            update_user: function(){
                if(!nullValues.includes(this.single_user.user_id)){
                    if(!nullValues.includes(this.single_user.username) && ["Admin","User"].includes(this.single_user.role)){
                        if(this.single_user.role == "User" && this.single_user.roles.length == 0){
                            alert("     ");
                        } else {
                            axios.delete("http://localhost:3000/roles/deleteRole/" + this.single_user.user_id).then(()=>{
                                axios.patch("http://localhost:3000/user/updateUser/" + this.single_user.user_id,{
                                    username: this.single_user.username,
                                    full_name: this.single_user.full_name,
                                    role: this.single_user.role,
                                    phone: this.single_user.phone
                                }).then(()=>{
                                    if(this.single_user.role == "User"){
                                        axios.post("http://localhost:3000/roles/addRole",{
                                            user_id: this.single_user.user_id,
                                            roles: this.single_user.roles.toString()
                                        });
                                    } else {
                                        this.single_user.roles = [];
                                    }
                                    alert("  ");
                                    let index = this.users.findIndex(obj => obj.user_id === this.single_user.user_id);
                                    this.users[index].username = this.single_user.username;
                                    this.users[index].full_name = this.single_user.full_name;
                                    this.users[index].role = this.single_user.role;
                                    this.users[index].phone = this.single_user.phone;
                                }).catch((err) => {
                                    showError(err);
                                });
                                    
                            });
                        }
                    } else {
                        alert("     ");
                    }
                } else {
                    alert("      ");
                }
            },
            update_password: function(){
                if(!nullValues.includes(this.single_user.user_id)){
                    if(!nullValues.includes(this.single_user.password) && this.single_user.password.length >= 6){
                        axios.patch("http://localhost:3000/user/updatePassword/" + this.single_user.user_id,{
                            password: this.single_user.user_id
                        }).then(() => {
                            alert("    ");
                            this.single_user.password = null;
                        }).catch((err) => {
                            showError(err);
                        });
                    } else {
                        alert("    6   ");
                    }
                } else {
                    alert("        ");
                }
            },
            update_image: function(){
                if(!nullValues.includes(this.single_user.user_id)){
                    if(!nullValues.includes(this.single_user.image_file)){
                        var form = new FormData();
                        form.append("user_image", this.single_user.image_file);
                        axios.patch("http://localhost:3000/user/updateImage/" + this.single_user.user_id, form).then(({data})=>{
                            let index = this.users.findIndex(obj => obj.user_id === this.single_user.user_id);
                            this.users[index].image_path = data.image_path;
                            this.single_user.image_path = data.image_path;
                            alert("   ");
                        }).catch((err)=>{
                            showError(err);
                        });
                    } else {
                        alert(" ");
                    }
                } else {
                    alert("       ");
                }
            },
            active_user: function(user_id){
                const c = confirm("      ");
                if(c){
                    axios.patch("http://localhost:3000/user/activeUser/" + user_id).then(()=>{
                        let index = this.users.findIndex(obj => obj.user_id === user_id);
                        this.users[index].status = 1;
                        alert("   ");
                    });
                }
            },
            disable_user: function(user_id){
                const c = confirm("     ");
                if(c){
                    axios.patch("http://localhost:3000/user/disableUser/" + user_id).then(()=>{
                        let index = this.users.findIndex(obj => obj.user_id === user_id);
                        this.users[index].status = 0;
                        alert("   ");
                    }).catch((err) => {
                        showError(err);
                    });
                }
            },
            add_user: function(){
                if(!nullValues.includes(this.single_user.username) && !nullValues.includes(this.single_user.password) && ["Admin","User"].includes(this.single_user.role)){
                    if(this.single_user.role == "User" && this.single_user.roles.length == 0){
                        alert("     ");
                    } else {
                        var form = new FormData();
                        form.append("username", this.single_user.username);
                        form.append("password", this.single_user.password);
                        form.append("full_name", this.single_user.full_name);
                        form.append("role", this.single_user.role);
                        form.append("phone", this.single_user.phone || "");
                        form.append("user_image", this.single_user.image_file);

                        axios.post("http://localhost:3000/user/addUser", form).then(({data}) => {
                            this.users.unshift({
                                user_id: data.user_id,
                                username: this.single_user.username,
                                password: this.single_user.password,
                                full_name: this.single_user.full_name,
                                role: this.single_user.role,
                                status: this.single_user.status,
                                reg_date: new Date().toISOString().split("T")[0],
                                phone: this.single_user.phone,
                                image_path: data.image_path
                            });
                            if(this.single_user.role=="User"){
                                axios.post("http://localhost:3000/roles/addRole",{
                                    user_id: data.user_id,
                                    roles: this.single_user.roles.toString()
                                }).then(() => {
                                    this.get_single_user(data.user_id);
                                });
                            } else {
                                this.get_single_user(data.user_id);
                            }
                        }).catch((err) => {
                            showError(err);
                        });
                    }
                } else if (this.single_user.password.length < 6){
                    alert("    6   ");
                } else {
                    alert("     ");
                }
            }
        },
        computed: {
            searched_users: function(){
                return this.users.filter((obj) => {
                    return obj.user_id.toString().match(this.search) || 
                    obj.username.match(this.search) || 
                    obj.full_name.match(this.search) || 
                    obj.role.match(this.search) || 
                    obj.phone.match(this.search) || 
                    obj.reg_date.match(this.search); 
                });
            }
        },
        filters: {
            user_status_filter(value){
                if(value==1){
                    return "Active";
                } 
                return "Disable";
            }
        }
    }
</script>

<style scoped>
    .phone{
        direction: ltr;
    }
</style>