<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight"></app-nav>
        <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper" style="display:none;">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <h3 class="text-dark mb-0"></h3>
                    <header-btns></header-btns>
                </div>
                <hr>
                <div class="row mb-2">
                    <div class="col-xl-3 col-md-4"> 
                        <span class="btn btn-success btn-block" data-toggle="modal" data-target="#mdl_food_detail">   &nbsp; <i class="fas fa-plus"></i> </span> 
                    </div>
                    <div class="col-xl-9 col-md-4"> 
                        <div class="float-left">
                            <input type="text" class="form-control text-center" dir="ltr" placeholder="... " v-model="search">
                        </div> 
                    </div>
                </div>
                <hr>
                <div class="table-responsive">
                    <table class="table table-bordered table-striped text-center text-dark">
                        <thead>
                            <th class="text-center" style="width: 8%;"></th>
                            <th class="text-center"> </th>
                            <th class="text-center">  </th>
                            <th class="text-center" style="width: 10%;"></th>
                            <th class="text-center" style="width: 15%;"> </th>
                            <th class="text-center" style="width: 8%;"> </th>
                            <th class="text-center" style="width: 5%;"></th>
                            <th class="text-center" style="width: 5%;"></th>
                        </thead>
                        <tbody>
                            <tr v-for="food in searched_foods" :key="food.food_id">
                                <td> {{ food.food_id }} </td>
                                <td> {{ food.food_name }} </td>
                                <td> {{ food.food_type }} </td>
                                <td> {{ food.price }} </td>
                                <td> {{ food.username }} </td>
                                <td> <div class="p-2 text-light" dir="ltr" style="display: block;" v-bind:style="{ 'background-color': food.color_value }"> {{ food.color_value  }} </div> </td>
                                <td> <span class="btn btn-primary btn-block" v-on:click="get_single_food(food.food_id)" data-toggle="modal" data-target="#mdl_food_detail"> <i class="fas fa-edit"></i> </span> </td>
                                <td> <span class="btn btn-danger btn-block" v-on:click="del_food(food.food_id)"> <i class="fas fa-trash"></i> </span> </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div class="modal" id="mdl_food_detail">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="mt-2"> </h5>
                        <button class="btn btn-danger rounded-circle" v-on:click="clear_food_inputs()" data-dismiss="modal"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="row mb-2" v-if="single_food.food_id != null">
                            <label class="col-sm-3 mt-2"> : </label>
                            <input type="number" readonly class="form-control col-sm-8" v-model="single_food.food_id">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="text" required class="form-control col-sm-8" placeholder="    ..." v-model="single_food.food_name">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2"> : </label>
                            <select class="form-control col-sm-8" required v-model="single_food.type_id">
                                <option v-for="type in food_types" :key="type.type_id" v-bind:value="type.type_id"> {{ type.type_name }} </option>
                            </select>
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <input type="number" min="0" required class="form-control col-sm-8" placeholder="    ..." v-model="single_food.price">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">: </label>
                            <input type="file" class="col-sm-8" @change="process_file($event)">
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">  : </label>
                            <textarea class="form-control col-sm-8" rows="3" v-model="single_food.note"></textarea>
                        </div>
                        <div class="row mb-2">
                            <label class="col-sm-3 mt-2">: </label>
                            <input type="color" class="col-sm-8 mt-1" v-model="single_food.color_value">
                        </div>
                        <hr>
                        <div class="row mb-2">
                            <div class="mb-2" v-bind:class="{ 'col-sm-4': single_food.food_id != null, 'col-sm-12': single_food.food_id == null }"> <span class="btn btn-success btn-block" v-on:click="add_food()">   &nbsp; <i class="fas fa-plus"></i> </span> </div>
                            <div class="col-sm-4 mb-2" v-if="single_food.food_id != null"> <span class="btn btn-warning btn-block" v-on:click="update_food()">  &nbsp; <i class="fas fa-edit"></i> </span> </div>
                            <div class="col-sm-4 mb-2" v-if="single_food.food_id != null"> <span class="btn btn-danger btn-block" v-on:click="del_food(single_food.food_id)">   &nbsp; <i class="fas fa-trash"></i> </span> </div>
                            <div class="mb-2" v-bind:class="{ 'col-sm-6': single_food.image_path != null, 'col-sm-12': single_food.image_path == null }" v-if="single_food.food_id != null"> <span class="btn btn-primary btn-block" v-on:click="update_image()">   &nbsp; <i class="fas fa-image"></i> </span> </div>
                            <div class="col-sm-6 mb-2" v-if="single_food.image_path != null"> <span class="btn btn-danger btn-block" v-on:click="del_image()">   &nbsp; <i class="fas fa-trash"></i> </span> </div>
                        </div>
                        <hr>
                        <div class="row mb-2">
                            <div class="col-sm-12" v-if="single_food.image_path != null"> 
                                <img v-bind:src="single_food.image_path" alt="No Image Found" class="w-100 h-100">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import showError from "../showError.js";
    import auth from "../auth.js";

    axios.defaults.withCredentials = true;

    const nullValues = [null, ""];
    export default {
        name: "foods",
        data(){
            return {
                foods: [],
                single_food: {
                    food_id: null,
                    food_name: null,
                    type_id: null,
                    price: null,
                    image_file: null,
                    image_path: null,
                    note: null,
                    color_value: null
                },
                food_types: [],
                user: {
                    user_id: null,
                    username: ""
                },
                search: ""
            }
        },
        beforeCreate(){
            auth("foods");
        },
        created(){
            axios.post("http://localhost:3000/foods/getData").then(({data}) => {
                this.foods = data;
            }).catch((err) => {
                showError(err);
            });

            axios.post("http://localhost:3000/food_type/getData").then(({data}) => {
                this.food_types = data;
            }).catch((err) => {
                showError(err);
            }); 

            axios.post("http://localhost:3000/getUser").then(({data}) => {
                this.user.user_id = data.user_id;
                this.user.username = data.username;
            }).catch((err) => {
                showError(err);
            }); 
        },
        methods:{
            process_file: function(event){
                this.single_food.image_file = event.target.files[0];
            },
            clear_food_inputs: function(){
                this.single_food.food_id = null;
                this.single_food.food_name = null;
                this.single_food.type_id = null;
                this.single_food.price = null;
                this.single_food.image_path = null;
                this.single_food.image_file = null;
                this.single_food.note = null;
                this.single_food.color_value = null;
            },
            get_single_food: function(food_id){
                const [food] = this.foods.filter(obj => obj.food_id === food_id);
                this.single_food.food_id = food.food_id;
                this.single_food.food_name = food.food_name;
                this.single_food.type_id = this.food_types.filter(obj => obj.type_name === food.food_type)[0].type_id;
                this.single_food.price = food.price;
                this.single_food.image_path = food.image_path;
                this.single_food.note = food.note;
                this.single_food.color_value = food.color_value;
            },
            add_food: function(){
                if(!nullValues.includes(this.single_food.food_name) && !nullValues.includes(this.single_food.type_id) && !nullValues.includes(this.single_food.price) && this.single_food.price > -1){
                    var form = new FormData();
                    form.append("food_name", this.single_food.food_name);
                    form.append("type_id", this.single_food.type_id);
                    form.append("price", this.single_food.price);
                    form.append("user_id", this.user.user_id);
                    form.append("food_image", this.single_food.image_file);
                    form.append("note", this.single_food.note);
                    form.append("color_value", this.single_food.color_value);
                    axios.post("http://localhost:3000/foods/addFood", form).then(({data})=>{
                        this.foods.unshift({
                            food_id: data.food_id,
                            food_name: this.single_food.food_name,
                            food_type: this.food_types.filter(obj => obj.type_id === this.single_food.type_id)[0].type_name,
                            price: this.single_food.price,
                            username: this.user.username,
                            image_path: data.image_path,
                            note: this.single_food.note,
                            color_value: this.single_food.color_value
                        });
                        this.get_single_food(data.food_id);
                    }).catch((err) => {
                        showError(err);
                    });
                } else {
                    alert("     ");
                }
            },
            update_food: function(){
                if(!nullValues.includes(this.single_food.food_id)){
                    if(!nullValues.includes(this.single_food.food_name) && !nullValues.includes(this.single_food.type_id) && !nullValues.includes(this.single_food.price) && this.single_food.price > -1){
                        axios.patch("http://localhost:3000/foods/updateFood/" + this.single_food.food_id, {
                            food_name: this.single_food.food_name,
                            type_id: this.single_food.type_id,
                            price: this.single_food.price,
                            note: this.single_food.note,
                            color_value: this.single_food.color_value
                        }).then(() => {
                            let index = this.foods.findIndex(obj => obj.food_id === this.single_food.food_id);
                            this.foods[index].food_name = this.single_food.food_name;
                            this.foods[index].price = this.single_food.price;
                            this.foods[index].food_type = this.food_types.filter(obj => obj.type_id === this.single_food.type_id)[0].type_name;
                            this.foods[index].note = this.single_food.note;
                            this.foods[index].color_value = this.single_food.color_value;
                            alert("  ");
                        }).catch((err) => {
                            showError(err);
                        });
                    } else {
                        alert("     ");
                    }
                } else {
                    alert("      ");
                }
            },
            update_image: function(){
                if(!nullValues.includes(this.single_food.food_id)){
                    if(!nullValues.includes(this.single_food.image_file)){
                        var form = new FormData();
                        form.append("food_image", this.single_food.image_file);
                        axios.patch("http://localhost:3000/foods/updateImage/" + this.single_food.food_id, form).then(({data}) => {
                            let index = this.foods.findIndex(obj => obj.food_id === this.single_food.food_id);
                            this.foods[index].image_path = data.image_path;
                            this.single_food.image_path = data.image_path;
                            alert("   ");
                        }).catch((err) => {
                            showError(err);
                        });
                    } else {
                        alert(" ");
                    }
                } else {
                    alert("       ");
                }
            },
            del_food: function(food_id){
                if(!nullValues.includes(food_id)){
                    var c = confirm("     ");
                    if(c){
                        axios.delete("http://localhost:3000/foods/deleteFood/" + food_id).then(() => {
                            this.foods = this.foods.filter(obj => obj.food_id != food_id);
                        }).catch((err) => {
                            console.log(err.response);
                            showError(err);
                        });
                    }
                } else {
                    alert("      ");
                }
            },
            del_image: function(){
                if(!nullValues.includes(this.single_food.food_id) && !nullValues.includes(this.single_food.food_id)){
                    var c = confirm("     ");
                    if(c){
                        axios.delete("http://localhost:3000/foods/deleteImage/" + this.single_food.food_id).then(() => {
                            let index = this.foods.findIndex(obj => obj.food_id === this.single_food.food_id);
                            this.foods[index].image_path = null;
                            this.single_food.image_path = null;
                        });
                    }
                } else {
                    alert("       ");
                }
            }
        },
        computed: {
            searched_foods: function(){
                return this.foods.filter((obj) => {
                    return obj.food_id.toString().match(this.search) || 
                    obj.food_name.match(this.search) || 
                    obj.food_type.match(this.search) || 
                    obj.price.toString().match(this.search) ||
                    obj.username.match(this.search);
                });
            }
        }
    }
</script>