<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight"></app-nav>
        <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper" style="display:none;">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <h3 class="text-dark mb-0"></h3>
                    <header-btns></header-btns>
                </div>
                <hr>
                <div class="row">
                    <div class="col-xl-4 col-md-4">
                        <label class="form-control text-center bg-info text-light">    </label>
                        <select class="form-control" v-model="table_num">
                            <option value="-1"></option>
                            <option v-for="table in tables" :key="table.table_num" v-bind:value="table.table_num"> {{ table.table_num }} </option>
                        </select>
                    </div>
                    <div class="col-xl-4 col-md-4">
                        <label class="form-control text-center bg-info text-light">   </label>
                        <label class="form-control text-center"> {{ invoice_id }}  </label>
                    </div>
                    <div class="col-xl-4 col-md-4">
                        <label class="form-control text-center bg-info text-light">  </label>
                        <select class="form-control" v-model="single_invoice.type" v-bind:disabled="invoice_id != null">
                            <option value="0"></option>
                            <option value="1"></option>
                            <option value="2"></option>
                        </select>
                    </div>
                </div>
                <div class="row">
                    <div class="col-xl-4 col-md-4">
                        <span class="btn btn-outline-warning rounded col-sm-4" data-toggle="modal" data-target="#mdl_change_table">   </span>
                        <span class="btn btn-outline-warning rounded col-sm-5 mr-1" data-toggle="modal" data-target="#mdl_delivery">   </span>
                    </div>
                </div>
                <hr>
                <div class="row">
                    <div class="col-xl-6" style="border: 1px solid; background-color: #dddddd;">
                        <div class=" flex-row bd-highlight" style=" overflow: auto; white-space: nowrap;">
                            <span v-for="food_type in food_types" :key="food_type.type_id" v-on:click="get_foods(food_type.type_id)" class="p-2 pl-4 pr-4 bd-highlight btn btn-info" style="border: 1px solid;"> {{ food_type.type_name }} </span>
                        </div>
                        <hr>
                        <div class="flex-wrap align-content-stretch bg-light" style="overflow:auto; margin-right:5px; height:445px;">
                            <span v-for="food in foods" :key="food.food_id" 
                                  v-on:click="set_food(food.food_id)" 
                                  class="p-4 bd-highlight btn col-sm-4 text-light food" 
                                  style="border: 1px solid; background-image: url('https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/delish-homemade-pizza-horizontal-1542312378.png'); background-size: cover;">
                                <div v-bind:style="{ 'background-color': hex_to_rgb(food.color_value)}">
                                    {{ food.food_name }}
                                    <hr>
                                    {{ food.price }}
                                </div>
                            </span> 
                        </div>
                        <span class="btn btn-warning btn-block mt-2 mb-2" data-toggle="modal" data-target="#mdl_kitchen">       <i class="fas fa-print"></i> </span>
                    </div>
                    <br>
                    <div class="col-xl-6">
                        <div class="table-responsive mb-2" style="height:380px; overflow:auto;">
                            <table class="table table-bordered table-striped text-center text-dark">
                                <thead class="bg-primary text-light">
                                    <th class="text-center">  </th>
                                    <th class="text-center">  </th>
                                    <th class="text-center">  </th>
                                    <th class="text-center">  </th>
                                    <th class="text-center">   </th>
                                    <th class="text-center" style="width:5%;">  </th>
                                    <th class="text-center" style="width:5%;">  </th>
                                </thead>
                                <tbody>
                                    <tr v-for="detail in invoice_details" :key="detail.id">
                                        <td> {{ detail.id }} </td>
                                        <td> {{ detail.food_name }} </td>
                                        <td> {{ detail.price }} </td>
                                        <td> {{ detail.qty }} </td>
                                        <td> {{ detail.all_price }} </td>
                                        <td> 
                                            <span class="btn btn-success btn-sm rounded mb-1" v-on:click="plus_invoice_food(detail.id)"> <i class="fas fa-arrow-up"></i> </span> 
                                            <span class="btn btn-danger btn-sm rounded" v-on:click="minus_invoice_food(detail.id)"> <i class="fas fa-arrow-down"></i> </span> 
                                        </td>
                                        <td> <span class="btn btn-danger btn-sm rounded-circle" v-on:click="delete_invoice_food(detail.id)"> &times; </span> </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div>
                            <div class="col-sm-4">
                                <label class="form-control bg-primary text-light text-center">  </label>
                                <input type="number" class="form-control text-center" min="0" v-model="single_invoice.discount">
                            </div>
                            <div class="col-sm-4">
                                <label class="form-control bg-primary text-light text-center">  </label>
                                <input type="number" class="form-control text-center" min="0" v-model="single_invoice.service">
                            </div>
                            <div class="col-sm-4">
                                <label class="form-control bg-primary text-light text-center">   </label>
                                <input type="number" class="form-control text-center" min="0" v-model="single_invoice.amount_paid">
                            </div>
                            <div class="col-sm-12 mt-2 mb-2">
                                <label class="form-control bg-primary text-light text-center">   </label>
                                <label class="form-control col-sm-9 bg-default text-dark text-center"> {{ single_invoice.total_price + Number(single_invoice.service) - Number(single_invoice.discount) + Number(delivery_info.price) }} </label> 
                                <span class="btn btn-success col-sm-3" v-on:click="sell_invoice()">  &nbsp; <i class="fas fa-dollar-sign"></i> </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="mdl_kitchen">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="text-dark mt-2">     </h5>
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="container-fluid">
                            <table class="table text-dark table-bordered">
                                <th class="text-center bg-primary text-light" v-if="invoice_id != null && table_num != -1"> . </th>
                                <th class="text-center" v-if="invoice_id != null && table_num != -1"> {{ table_num }} </th>
                                <th class="text-center bg-primary text-light" v-if="invoice_id != null"> . </th>
                                <th class="text-center" v-if="invoice_id != null"> {{ invoice_id }} </th>
                            </table>
                            <hr>
                            <table class="table text-dark text-center table-bordered table-striped">
                                <thead>
                                    <th class="text-center">  </th>
                                    <th class="text-center" style="width:30%;">  </th>
                                    <th class="text-center" style="width:5%;">  </th>
                                </thead>
                                <tbody>
                                    <tr v-for="kitchen_food in kitchen_foods" :key="kitchen_food.food_name">
                                        <td> {{ kitchen_food.food_name }} </td>
                                        <td> 
                                            <span class="btn btn-success btn-sm rounded" v-on:click="plus_kitchen_food(kitchen_food.food_name)"> <i class="fas fa-plus"></i> </span>
                                            <span class="ml-5 mr-5"> {{ kitchen_food.qty }} </span>
                                            <span class="btn btn-danger btn-sm rounded" v-on:click="minus_kitchen_food(kitchen_food.food_name)"> <i class="fas fa-minus"></i> </span>
                                        </td>
                                        <td> <span class="btn btn-danger rounded-circle" v-on:click="delete_kitchen_food(kitchen_food.food_name)"> &times; </span> </td>
                                    </tr>
                                </tbody>
                            </table>
                            <span class="btn btn-success btn-block rounded" v-if="invoice_id != null && kitchen_foods.length > 0">   &nbsp; <i class="fas fa-print"></i> </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="mdl_change_table">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal"> &times; </button>
                    </div>
                    <div class="modal-body text-dark">
                        <div class="container-fluid">
                            <div class="row">
                                <label class="col-sm-12 text-center">    </label>
                                <select class="form-control col-sm-12" v-model="new_table_num">
                                    <option value="-1"></option>
                                    <option v-for="table in tables" :key="table.table_num" v-bind:value="table.table_num"> {{ table.table_num }} </option>
                                </select>
                                <span class="btn btn-primary btn-block rounded mt-1" v-on:click="change_table()">  </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="mdl_delivery">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal"> &times; </button>
                    </div>
                    <div class="modal-body text-dark">
                        <div class="container-fluid">
                            <div class="row mb-2">
                                <label class="col-sm-3 mt-2 text-center">  :  </label>
                                <input type="text" class="form-control col-sm-9 text-center" v-model="delivery_info.address">
                            </div>
                            <div class="row mb-2">
                                <label class="col-sm-3 mt-2 text-center">  :  </label>
                                <input type="text" class="form-control col-sm-9 text-center" v-model="delivery_info.phone">
                            </div>
                            <div class="row mb-2">
                                <label class="col-sm-3 mt-2 text-center">  :  </label>
                                <input type="number" min="0" class="form-control col-sm-9 text-center" v-model="delivery_info.price">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import showError from "../showError.js";
    import auth from "../auth.js";

    axios.defaults.withCredentials = true;

    export default {
        name: "cashier",
        data(){
            return {
                tables: [],
                food_types: [],
                foods: [],
                invoice_details: [],
                table_num: -1,
                new_table_num: -1,
                invoice_id: null,
                kitchen_foods: [],
                single_invoice: {
                    invoice_id: null,
                    type: "0",
                    status: "0",
                    discount: 0,
                    service: 0,
                    total_price: 0,
                    amount_paid: 0
                },
                delivery_info: {
                    address: "",
                    phone: "",
                    price: 0
                }
            }
        },
        beforeCreate(){
            auth("cashier");
        },
        created(){
            axios.post("http://localhost:3000/tables/getData").then(({data}) => {
                this.tables = data;
            });
            axios.post("http://localhost:3000/food_type/getData").then(({data}) => {
                this.food_types = data;
            }).catch((err) => {
                showError(err);
            });
        },
        methods: {
            clear_single_invoice: function(){
                this.single_invoice.invoice_id = null;
                this.single_invoice.type = "0";
                this.single_invoice.status = "0";
                this.single_invoice.total_price = 0;
                this.single_invoice.amount_paid = 0;
                this.single_invoice.discount = 0;
                this.single_invoice.service = 0; 
                this.kitchen_foods = [];
                this.delivery_info.price = 0;
                this.delivery_info.address = "";
                this.delivery_info.phone = "";
            },
            get_foods: function(type_id){
                axios.post("http://localhost:3000/foods/getDataByType/" + type_id).then(({data}) => {
                    this.foods = data;
                }).catch((err) => {
                    showError(err);
                });
            },
            plus_kitchen_food: function(food_name){
                let index = this.kitchen_foods.findIndex(obj => obj.food_name == food_name);
                if(index != -1){
                    this.kitchen_foods[index].qty += 1;
                }
            },
            minus_kitchen_food: function(food_name){
                let index = this.kitchen_foods.findIndex(obj => obj.food_name == food_name);
                if(index != -1){
                    this.kitchen_foods[index].qty -= 1;
                }
            },
            delete_kitchen_food: function(food_name){
                var c = confirm("         ");
                if(c){
                    this.kitchen_foods = this.kitchen_foods.filter(obj => obj.food_name != food_name);
                }
            },
            delete_invoice_food: function(invoice_detail_id){
                var c = confirm("     ");
                if(c){
                    axios.delete("http://localhost:3000/cashier/deleteInvoiceFood/" + invoice_detail_id + "/" + this.invoice_id).then(({data}) => {
                        if(data.message == "Food Deleted"){
                            const [single_detail] = this.invoice_details.filter(obj => obj.id == invoice_detail_id);
                            this.single_invoice.total_price -= Number(single_detail.all_price);
                            this.single_invoice.amount_paid -= Number(single_detail.all_price);
                            this.invoice_details = this.invoice_details.filter(obj => obj.id != invoice_detail_id);
                        } else if(data.message == "Invoice Deleted"){
                            this.single_invoice.total_price = 0;
                            this.single_invoice.amount_paid = 0;
                            this.invoice_details = [];
                            this.invoice_id = null;
                        }
                    }).catch((err) => {
                        console.log(err.response.data);
                    });
                }
            },
            plus_invoice_food: function(id){
                if(this.single_invoice.status == "1"){
                    this.$swal("", "       ", "error");
                } else {
                    const index = this.invoice_details.findIndex(obj => obj.id == id);
                    axios.patch("http://localhost:3000/cashier/plusFood/" + id + "/" + this.invoice_id).then(() => {
                        this.invoice_details[index].qty += 1;
                        this.invoice_details[index].all_price += this.invoice_details[index].price;
                        this.single_invoice.total_price += this.invoice_details[index].price;
                        this.single_invoice.amount_paid += this.invoice_details[index].price;
                    }).catch((err) => {
                        showError(err);
                    });
                }
            },
            minus_invoice_food: function(id){
                if(this.single_invoice.status == "1"){
                    this.$swal("", "       ", "error");
                } else {
                    const index = this.invoice_details.findIndex(obj => obj.id == id);
                    if(this.invoice_details.length == 1 && this.invoice_details[index].qty == 1){
                        var c = confirm("           ");
                        if(c){
                            axios.delete("http://localhost:3000/cashier/minusFood/" + id + "/" + this.invoice_id).then(({data}) => {
                                if(data.message == "Food Deleted"){
                                    this.invoice_details = this.invoice_details.filter(obj => obj.id != id);
                                    this.single_invoice.total_price -= this.invoice_details[index].price;
                                    this.single_invoice.amount_paid -= this.invoice_details[index].price;
                                } else if(data.message == "Food Decreased"){
                                    this.invoice_details[index].qty -= 1;
                                    this.single_invoice.total_price -= this.invoice_details[index].price;
                                    this.single_invoice.amount_paid -= this.invoice_details[index].price;
                                    this.invoice_details[index].all_price -= this.invoice_details[index].price;
                                } else if(data.message == "Invoice Deleted"){
                                    this.invoice_details = [];
                                    this.clear_single_invoice();
                                    this.invoice_id = null;
                                }
                            }).catch((err) => {
                                showError(err);
                            });
                        }
                    } else {
                        axios.delete("http://localhost:3000/cashier/minusFood/" + id + "/" + this.invoice_id).then(({data}) => {
                            if(data.message == "Food Deleted"){
                                this.single_invoice.total_price -= this.invoice_details[index].price;
                                this.single_invoice.amount_paid -= this.invoice_details[index].price;
                                this.invoice_details = this.invoice_details.filter(obj => obj.id != id);
                            } else if(data.message == "Food Decreased"){
                                this.invoice_details[index].qty -= 1;
                                this.single_invoice.total_price -= this.invoice_details[index].price;
                                this.single_invoice.amount_paid -= this.invoice_details[index].price;
                                this.invoice_details[index].all_price -= this.invoice_details[index].price;
                            } else if(data.message == "Invoice Deleted"){
                                this.invoice_details = [];
                                this.clear_single_invoice();
                                this.invoice_id = null;
                            }
                        }).catch((err) => {
                            showError(err);
                        });
                    }
                }
            },
            set_food: function(food_id){
                if(this.single_invoice.type == "0" && this.table_num == -1){
                    this.$swal("", "   ", "warning");
                } else {
                    const [single_food] = this.foods.filter(obj => obj.food_id == food_id);
                    axios.post("http://localhost:3000/cashier/setFood", {
                        invoice_id: this.invoice_id,
                        type: this.single_invoice.type,
                        table_num: this.table_num,
                        food_id
                    }).then(({data}) => {
                        if(data.message == "Inserted"){
                            if(data.invoice_id){
                                this.invoice_id = data.invoice_id;
                            }
                            this.invoice_details.unshift({
                                id: data.id,
                                food_name: single_food.food_name,
                                price: single_food.price,
                                qty: 1,
                                all_price: single_food.price
                            });
                            this.single_invoice.total_price += single_food.price;
                            this.single_invoice.amount_paid += single_food.price;
                        } else if(data.message == "Updated"){
                            const detail_index = this.invoice_details.findIndex(obj => obj.food_name == single_food.food_name);

                            this.invoice_details[detail_index].qty += 1;
                            this.invoice_details[detail_index].all_price += single_food.price;

                            this.single_invoice.total_price += single_food.price;
                            this.single_invoice.amount_paid += single_food.price;
                        }
                        let index = this.kitchen_foods.findIndex(obj => obj.food_name == single_food.food_name);
                        if(index == -1){
                            this.kitchen_foods.push({
                                food_name: single_food.food_name,
                                qty: 1
                            });
                        } else {
                            this.kitchen_foods[index].qty += 1;
                        }
                    }).catch((err) => {
                        showError(err);
                    });
                }
            },
            change_table: function() {
                if(this.new_table_num != -1){
                    axios.patch("http://localhost:3000/cashier/changeTable/" + this.invoice_id, {
                        old_table: this.table_num,
                        new_table: this.new_table_num
                    }).then(() => {
                        this.table_num = this.new_table_num;
                        this.new_table_num = -1;
                        this.$swal("", "  ", "success");
                    }).catch((err) => {
                        showError(err);
                    });
                } else if(this.new_table_num == -1 || this.new_table_num == this.table_num) {
                   this.$swal("", "    ", "warning");
                }
            },
            sell_invoice: function(){
                if(this.invoice_id != null){
                    if(this.single_invoice.type == "2" && (this.delivery_info.address == "" || this.delivery_info.phone == "" || this.delivery_info.price < 0)){
                        this.$swal("", "     ", "warning");
                    } else {
                        axios.post("http://localhost:3000/cashier/sellInvoice", {
                            invoice_id: this.invoice_id,
                            total_price: this.single_invoice.total_price + Number(this.single_invoice.service) - Number(this.single_invoice.discount) + Number(this.delivery_info.price),
                            amount_paid: this.single_invoice.amount_paid,
                            service: this.single_invoice.service,
                            discount: this.single_invoice.discount,
                            address: this.delivery_info.address,
                            phone: this.delivery_info.phone,
                            delivery_price: this.delivery_info.price
                        }).then(() => {
                            this.$swal("", " ", "success");
                            // Print Invoice
                            this.invoice_id = null;
                            this.clear_single_invoice();
                            this.invoice_details = [];
                        }).catch((err) => {
                            showError(err);
                        });
                    }
                } else {
                    this.$swal("", "    ", "warning");
                }
            },
            hex_to_rgb: function(hex){
                if(hex != null && hex!= "null"){
                    var rgb = hex.replace(/^#?([a-f\d])([a-f\d])([a-f\d])$/i,(m, r, g, b) => '#' + r + r + g + g + b + b)
                    .substring(1).match(/.{2}/g)
                    .map(x => parseInt(x, 16)).join(",");
                    
                    return "rgba("+ rgb + ",0.3)";
                } else {
                    return "rgba(0,0,0,0.3)";
                }
            }
        },
        watch: {
            table_num: function(value){
                if(value > 0){
                    axios.post("http://localhost:3000/cashier/getInvoiceByTable/" + value).then(({data}) => {
                        if(data.invoice){
                            this.single_invoice.invoice_id = data.invoice.invoice_id;
                            this.single_invoice.type = data.invoice.type;
                            this.single_invoice.status = data.invoice.status;
                            this.single_invoice.total_price = data.total_price;
                            this.single_invoice.amount_paid = data.total_price;
                            this.invoice_id = data.invoice.invoice_id;
                            this.invoice_details = data.invoice_foods || [];
                            this.kitchen_foods = [];
                        } else {
                            this.clear_single_invoice();
                            this.invoice_id = null;
                            this.invoice_details = [];
                        }
                    }).catch((err) => {
                        showError(err);
                    });
                } else {
                    this.clear_single_invoice();
                    this.invoice_id = null;
                    this.invoice_details = [];
                }
            },
            'single_invoice.service': function(value){
                this.single_invoice.amount_paid = this.single_invoice.total_price + Number(value) - Number(this.single_invoice.discount) + Number(this.delivery_info.price);
            },
            'single_invoice.discount': function(value){
                this.single_invoice.amount_paid = this.single_invoice.total_price + Number(this.single_invoice.service) - Number(value) + Number(this.delivery_info.price);
            },
            'delivery_info.price': function(value){
                this.single_invoice.amount_paid = this.single_invoice.total_price + Number(this.single_invoice.service) - Number(this.single_invoice.discount) + Number(value);
            }
        }
    }
</script>