<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight"></app-nav>
        <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper" style="display:none;">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <h3 class="text-dark mb-0"> </h3>
                    <header-btns></header-btns>
                </div>
                <hr>
                <div class="row">
                    <div class="col-md-4 col-xl-3 mb-4">
                        <div class="card shadow border-left-success py-2">
                            <div class="card-body">
                                <div class="row align-items-center no-gutters">
                                    <div class="col mr-2 text-right">
                                        <div class="text-primary font-weight-bold text-md mb-2"><span>    </span></div>
                                        <div class="text-dark font-weight-bold h4 mb-0"> <span> {{ daily_sold | money_filter }} </span> IQD </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-4 col-xl-3 mb-4">
                        <div class="card shadow border-left-success py-2">
                            <div class="card-body">
                                <div class="row align-items-center no-gutters">
                                    <div class="col mr-2 text-right">
                                        <div class="text-primary font-weight-bold text-md mb-2"><span>   </span></div>
                                        <div class="text-dark font-weight-bold h4 mb-0"> <span> {{ (daily_sold / dollar_price).toFixed(1) }} </span> $ </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-4 col-xl-3 mb-4">
                        <div class="card shadow border-left-primary py-2">
                            <div class="card-body">
                                <div class="row align-items-center no-gutters">
                                    <div class="col mr-2 text-right">
                                        <div class="text-primary font-weight-bold text-md mb-2"><span>  </span></div>
                                        <div class="text-dark font-weight-bold h4 mb-0"> <span> {{ daily_invoice }} </span>  </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <hr>
                <div class="row mb-2">
                    <div class="col-xl-2 col-md-3 mb-2"> <span class="btn btn-success btn-block shadow" v-on:click="get_all()">   </span> </div>
                    <div class="col-xl-2 col-md-3 mb-2"> <span class="btn btn-primary btn-block shadow" v-on:click="get_daily()">   </span> </div>
                    <div class="col-xl-2 col-md-3 mb-2"> <span class="btn btn-primary btn-block shadow" v-on:click="get_weekly()">    </span> </div>
                    <div class="col-xl-2 col-md-3 mb-2"> <span class="btn btn-primary btn-block shadow" v-on:click="get_monthly()">    </span> </div>
                    <div class="col-xl-2 col-md-3 mb-2"> <span class="btn btn-warning btn-block shadow" data-toggle="modal" data-target="#mdl_search_date">   </span> </div>
                </div>
                <hr>
                <div class="table-responsive">
                    <table class="table table-bordered table-striped text-dark text-center" id="tbl_invoices">
                        <thead>
                            <th class="text-center" style="width: 10%;">. </th>
                            <th class="text-center" style="width: 9%;"></th>
                            <th class="text-center" style="width: 9%;"> </th>
                            <th class="text-center" style="width: 10%;"> </th>
                            <th class="text-center" style="width: 10%;"> </th>
                            <th class="text-center" style="width: 12%;">   </th>
                            <th class="text-center" style="width: 12%;">   </th>
                            <th class="text-center" style="width: 12%;"></th>
                            <th class="text-center" style="width: 12%;"></th>
                            <th class="text-center" style="width: 8%;"></th>
                        </thead>
                        <tbody>
                            <tr v-for="invoice in invoices" :key="invoice.invoice_id">
                                <td> {{ invoice.invoice_id }} </td>
                                <td> {{ invoice.type | type_filter }} </td>
                                <td> {{ invoice.status | status_filter }} </td>
                                <td> {{ invoice.discount }} </td>
                                <td> {{ invoice.service }} </td>
                                <td> {{ invoice.total_price }} </td>
                                <td> {{ (invoice.total_price / dollar_price).toFixed(1) }} </td>
                                <td> {{ invoice.username }} </td>
                                <td> {{ invoice.invoice_date.slice(0,10) }} </td>
                                <td> <span class="btn btn-primary text-center" v-on:click="get_invoice_detail(invoice.invoice_id)" data-toggle="modal" data-target="#mdl_single_invoice"> <i class="fas fa-eye"></i> </span> </td>
                            </tr>
                        </tbody>
                    </table>

                    <div id="pagination-btns-all" style="display:none" class="p-1 m-1" dir="ltr"> 
                        <button class="btn btn-primary m-1" v-on:click="previous_pagination_btns('pagination-btns-all')">  </button>
                        <button class="btn btn-outline-primary m-1 pagination-btns"
                                style="display:none;"
                                v-for="num in Math.ceil(all_invoice / 25)" :key="num"
                                v-bind:class="'pg-group-' + Math.ceil(num / 5)"
                                v-on:click="get_limited_data_all(num)"> {{ num }} </button>
                        <button class="btn btn-primary m-1" v-on:click="next_pagination_btns('pagination-btns-all')">  </button>
                    </div>

                    <div id="pagination-btns-week" style="display:none" class="p-1 m-1" dir="ltr"> 
                        <button class="btn btn-primary m-1" v-on:click="previous_pagination_btns('pagination-btns-week')">  </button>
                        <button class="btn btn-outline-primary m-1 pagination-btns"
                                style="display:none;"
                                v-for="num in Math.ceil(weekly_invoice / 25)" :key="num"
                                v-bind:class="'pg-group-' + Math.ceil(num / 5)"
                                v-on:click="get_limited_data_week(num)"> {{ num }} </button>
                        <button class="btn btn-primary m-1" v-on:click="next_pagination_btns('pagination-btns-week')">  </button>
                    </div>

                    <div id="pagination-btns-month" style="display:none" class="p-1 m-1" dir="ltr"> 
                        <button class="btn btn-primary m-1" v-on:click="previous_pagination_btns('pagination-btns-month')">  </button>
                        <button class="btn btn-outline-primary m-1 pagination-btns"
                                style="display:none;"
                                v-for="num in Math.ceil(monthly_invoice / 25)" :key="num"
                                v-bind:class="'pg-group-' + Math.ceil(num / 5)"
                                v-on:click="get_limited_data_month(num)"> {{ num }} </button>
                        <button class="btn btn-primary m-1" v-on:click="next_pagination_btns('pagination-btns-month')">  </button>
                    </div>
                </div>
            </div>
            
        </div>
        <div class="modal text-dark" id="mdl_single_invoice">
            <div class="modal-dialog modal-xl">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="mt-2">    : &nbsp; <span> {{ single_invoice.invoice_id }} </span> </h5>
                        <button class="btn btn-danger rounded-circle" v-on:click="clear_single_invoice()" data-dismiss="modal"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="container-fluid">
                            <div class="row mb-2">
                                <div class="col-xl-2 text-center p-2 mb-2 alert alert-primary" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.type | type_filter }} </span> </label>
                                </div>
                                <div class="col-xl-2 text-center p-2 mb-2 alert alert-primary" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.table_num }} </span> </label>
                                </div>
                                <div class="col-xl-2 text-center p-2 mb-2 alert alert-primary" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.status | status_filter }} </span> </label>
                                </div>                                
                                <div class="col-xl-2 text-center p-2 mb-2 alert alert-primary" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.discount }} </span> </label>
                                </div>
                                <div class="col-xl-2 text-center p-2 mb-2 alert alert-primary" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.service }} </span> </label>
                                </div>
                                <div class="col-xl-2 text-center p-2 mb-2 alert alert-primary" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.username }} </span> </label>
                                </div>
                                <div class="col-xl-2 text-center p-2 mb-2 alert alert-primary" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.invoice_date.slice(0, 10) }} </span> </label>
                                </div>
                                <div class="col-xl-4 text-center p-2 mb-2 alert alert-success" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.total_price }} </span> </label>
                                </div>
                            </div>
                            <hr>
                            <div class="row mb-2" id="invoice_delivery_info" v-if="single_invoice.delivery_info.delivery_id != null">
                                <div class="col-xl-12 text-right mb-2">   : </div>

                                <div class="col-xl-2 text-center p-2 mb-1 alert alert-warning" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.delivery_info.delivery_id }} </span> </label>
                                </div>
                                <div class="col-xl-4 text-center p-2 mb-1 alert alert-warning" style="border: 1px solid;">
                                    <label>  : &nbsp; <span> {{ single_invoice.delivery_info.address }} </span> </label>
                                </div>
                                <div class="col-xl-2 text-center p-2 mb-1 alert alert-warning" style="border: 1px solid;">
                                    <label> : &nbsp; <span dir="ltr"> {{ single_invoice.delivery_info.phone }} </span> </label>
                                </div>
                                <div class="col-xl-2 text-center p-2 mb-1 alert alert-warning" style="border: 1px solid;">
                                    <label>  : &nbsp; <span dir="ltr"> {{ single_invoice.delivery_info.delivery_price }} </span> </label>
                                </div>
                            </div>
                            <hr v-if="single_invoice.delivery_info.delivery_id != null">
                            <div class="row mb-2">
                                <div class="table-responsive">
                                    <table class="table table-bordered table-striped text-center text-dark">
                                        <thead>
                                            <th class="text-center" style="width: 8%;">  </th>
                                            <th class="text-center" style="width: 20%;">  </th>
                                            <th class="text-center" style="width: 10%;">  </th>
                                            <th class="text-center" style="width: 13%;">  </th>
                                            <th class="text-center" style="width: 16%;">   </th>
                                            <th class="text-center">  </th>
                                        </thead>
                                        <tbody>
                                            <tr v-for="food in single_invoice.foods" :key="food.id">
                                                <td> {{ food.id }} </td>
                                                <td> {{ food.food_name }} </td>
                                                <td> {{ food.qty }} </td>
                                                <td> {{ food.price }} </td>
                                                <td> {{ food.all_price }} </td>
                                                <td> {{ food.note }} </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal text-dark" id="mdl_search_date">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="mt-2">     </h5>
                        <button class="btn btn-danger rounded-circle" data-dismiss="modal"> &times; </button>
                    </div>
                    <div class="modal-body">
                        <div class="container-fluid">
                            <div class="row mb-2">
                                <label class="col-sm-2 mt-2"> : </label>
                                <input type="date" id="" class="form-control col-sm-9">
                            </div>
                            <div class="row mb-2">
                                <label class="col-sm-2 mt-2"> : </label>
                                <input type="date" id="" class="form-control col-sm-9">
                            </div>
                            <div class="row mb-2">
                                <span class="btn btn-primary btn-block">   &nbsp; <i class="fas fa-search"></i> </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import showError from "../showError.js";
    import auth from "../auth.js";

    axios.defaults.withCredentials = true;

    export default {
        name:"reports",
        data(){
            return{
                daily_sold: 0,
                daily_invoice: 0,
                weekly_invoice: 0,
                monthly_invoice: 0,
                all_invoice: 0,
                dollar_price: 0,
                invoices: [],
                single_invoice: {
                    invoice_id: null,
                    type: null,
                    table_num: null,
                    status: null,
                    total_price: null,
                    discount: null,
                    service: null,
                    username: null,
                    invoice_date: "",
                    delivery_info: {
                        delivery_id: null,
                        address: null,
                        phone: null,
                        delivery_price: null
                    },
                    foods: []
                },
                pagination_btn: 1,
                max_pagination_btn: 1
            }
        },
        beforeCreate(){
            auth("reports");
        },
        created(){
            axios.post("http://localhost:3000/invoice/getDailyInvoice").then(({data}) => {
                this.daily_sold = data.daily_sold || 0;
                this.invoices = data.invoices;
            }).catch((err) => {
                showError(err);
            });
            axios.post("http://localhost:3000/invoice/getNoOfInvoices").then(({data}) => {
                this.all_invoice = data.all_invoice;
                this.daily_invoice = data.daily_invoice;
                this.weekly_invoice = data.weekly_invoice;
                this.monthly_invoice = data.monthly_invoice;
            }).catch((err) => {
                showError(err);
            });
            axios.post("http://localhost:3000/dollar/getData").then(({data}) => {
                this.dollar_price = data.dollar_price;
            });
        },
        methods: {
            previous_pagination_btns: function(pagination_type_id){
                if(this.pagination_btn > 1){
                    var el = document.getElementById(pagination_type_id);
                    var btns_to_hide = el.getElementsByClassName("pg-group-" + this.pagination_btn);
                    for (let index = 0; index < btns_to_hide.length; index++) {
                        btns_to_hide[index].style.display = "none";
                    }
                    this.pagination_btn -= 1;
                    var btns_to_show = el.getElementsByClassName("pg-group-" + this.pagination_btn);
                    for (let index = 0; index < btns_to_show.length; index++) {
                        btns_to_show[index].style.display = "inline";
                    }
                }
            },
            next_pagination_btns: function(pagination_type_id){
                if(this.pagination_btn < this.max_pagination_btn){
                    var el = document.getElementById(pagination_type_id);
                    var btns_to_hide = el.getElementsByClassName("pg-group-" + this.pagination_btn);
                    for (let index = 0; index < btns_to_hide.length; index++) {
                        btns_to_hide[index].style.display = "none";
                    }
                    this.pagination_btn += 1;
                    var btns_to_show = el.getElementsByClassName("pg-group-" + this.pagination_btn);
                    for (let index = 0; index < btns_to_show.length; index++) {
                        btns_to_show[index].style.display = "inline";
                    }
                }
            },
            clear_single_invoice: function(){
                this.single_invoice.invoice_id = null;
                this.single_invoice.type = null;
                this.single_invoice.table_num = null;
                this.single_invoice.status = null;
                this.single_invoice.total_price = null;
                this.single_invoice.discount = null;
                this.single_invoice.service = null;
                this.single_invoice.username = null;
                this.single_invoice.invoice_date = "";

                this.single_invoice.foods = [];

                this.single_invoice.delivery_info.delivery_id = null;
                this.single_invoice.delivery_info.address = null;
                this.single_invoice.delivery_info.phone = null;
                this.single_invoice.delivery_info.delivery_price = null;
            },
            get_limited_data_all: function(paginate_number){
                var offset = 0;
                if(paginate_number != 1){
                    offset = (paginate_number - 1) * 10;
                }
                axios.post("http://localhost:3000/invoice/getAllInvoice", {
                    offset
                }).then(({data}) => {
                    this.invoices = data.invoices;
                }).catch((err) => {
                    showError(err);
                });
            },
            get_limited_data_week: function(paginate_number){
                var offset = 0;
                if(paginate_number != 1){
                    offset = (paginate_number - 1) * 10;
                }
                axios.post("http://localhost:3000/invoice/getWeeklyInvoice", {
                    offset
                }).then(({data}) => {
                    this.invoices = data.invoices;
                }).catch((err) => {
                    showError(err);
                });
            },
            get_limited_data_month: function(paginate_number){
                var offset = 0;
                if(paginate_number != 1){
                    offset = (paginate_number - 1) * 10;
                }
                axios.post("http://localhost:3000/invoice/getMonthlyInvoice", {
                    offset
                }).then(({data}) => {
                    this.invoices = data.invoices;
                }).catch((err) => {
                    showError(err);
                });
            },
            get_all: function(){
                document.getElementById("pagination-btns-week").style.display = "none";
                document.getElementById("pagination-btns-month").style.display = "none";
                this.get_limited_data_all(1);
                document.getElementById("pagination-btns-all").style.display = "block";
            },
            get_daily: function(){
                document.getElementById("pagination-btns-week").style.display = "none";
                document.getElementById("pagination-btns-month").style.display = "none";
                document.getElementById("pagination-btns-all").style.display = "none";
                axios.post("http://localhost:3000/invoice/getDailyInvoice").then(({data}) => {
                    this.invoices = data.invoices;
                }).catch((err) => {
                    showError(err);
                });
            },
            get_weekly: function(){
                document.getElementById("pagination-btns-month").style.display = "none";
                document.getElementById("pagination-btns-all").style.display = "none";
                this.get_limited_data_week(1);
                document.getElementById("pagination-btns-week").style.display = "block";
            },
            get_monthly: function(){
                document.getElementById("pagination-btns-week").style.display = "none";
                document.getElementById("pagination-btns-all").style.display = "none";
                this.get_limited_data_month(1);
                document.getElementById("pagination-btns-month").style.display = "block";
            },
            get_invoice_detail: function(invoice_id){
                axios.post("http://localhost:3000/invoice/getInvoiceDetail/" + invoice_id).then(({data}) => {
                    this.single_invoice.invoice_id = data.invoice_detail.invoice_id;
                    this.single_invoice.type = data.invoice_detail.type;
                    this.single_invoice.table_num = data.invoice_detail.table_num;
                    this.single_invoice.total_price = data.invoice_detail.total_price;
                    this.single_invoice.discount = data.invoice_detail.discount;
                    this.single_invoice.service = data.invoice_detail.service;
                    this.single_invoice.username = data.invoice_detail.username;
                    this.single_invoice.invoice_date = data.invoice_detail.invoice_date;

                    this.single_invoice.foods = data.foods;
                    
                    if(data.delivery_info){
                        this.single_invoice.delivery_info.delivery_id = data.delivery_info.delivery_id;
                        this.single_invoice.delivery_info.address = data.delivery_info.address;
                        this.single_invoice.delivery_info.phone = data.delivery_info.phone;
                        this.single_invoice.delivery_info.delivery_price = data.delivery_info.delivery_price;
                    }

                });
            }
        },
        filters: {
            type_filter: function(value){
                if(value == 0 ){
                    return "";
                } else if (value == 1){
                    return "";
                }
                return "";
            },
            status_filter: function(value){
                if(value == 0){
                    return "";
                }
                return "";
            },
            money_filter: function(value){
                var arr = value.toString().split("").reverse();
                var arr2 = [];
                for(let i=0; i < arr.length; i++){
                    arr2.unshift(arr[i]);
                    if((i + 1) % 3 == 0){
                        arr2.unshift(",");
                    }
                }
                var number_with_comma = arr2.join("");
                if(number_with_comma.charAt(0) == ','){
                    return number_with_comma.slice(1);
                }
                return number_with_comma;
            }
        }
    };
</script>