<template>
    <div class="container animate__animated animate__fadeInDown">
        <div class="row justify-content-center">
            <div class="col-md-9 col-lg-12 col-xl-10">
                <div class="card shadow-lg o-hidden border-0 mt-5">
                    <div class="card-body p-0">
                        <div class="row">
                            <div class="col-lg-6 d-none d-lg-flex">
                                <div class="flex-grow-1 bg-login-image" id="image"></div>
                            </div>
                            <div class="col-lg-6">
                                <div class="p-5">
                                    <div class="text-center">
                                        <h4 class="text-dark mb-4"></h4>
                                    </div>
                                    <form class="user">
                                        <div class="form-group"><input v-model="username" class="form-control form-control-user" type="text" id="exampleInputEmail"  placeholder=" " ></div>
                                        <div class="form-group"><input v-model="password" class="form-control form-control-user" type="password" id="exampleInputPassword" placeholder=" " name="password"></div>
                                        <button class="btn btn-primary btn-block text-white btn-user" v-on:click.prevent="login()" type="submit" v-bind:disabled="validated"></button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <h6 class="text-center mt-5 text-dark"> Developed By: <a href="https://www.facebook.com/SulyTech19" target="_blank"> Suly Tech </a> - 0770 580 0707  </h6>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import showError from '../showError';
    axios.defaults.withCredentials = true;
    export default {
        name:"login",
        data(){
            return {
                username: "",
                password: ""
            }
        },
        beforeCreate(){
            axios.post("http://localhost:3000/isLogged").then(({data}) => {
                if(data.message == "Logged"){
                    window.location.href = "/";
                }
            });
        },
        mounted() {
            window.onclose = function () {
                axios.get("/close").then(() => {});
            }
        },
        computed: {
            validated: function(){
                return this.password.length < 6 && this.username.length == 0;
            }
        },
        methods: {
            login: function(){
                if(this.username == ""){
                    this.$swal("", "  ", "warning");
                } else if(this.password == "" || this.password.length < 6){
                    this.$swal("", "    6   ", "warning");
                } else {
                    axios.post("http://localhost:3000/login",{
                        username: this.username,
                        password: this.password
                    }).then(({data}) => {
                        console.log(data);
                        if(data.message == "Login Success"){
                            window.location.href = "/";
                        }
                    }).catch((err) => {
                        showError(err);
                    });
                }
            }
        }
    }
</script>

<style scoped>
    #image{
        background-image: url('../assets/img/fastfood.png');
    }
</style>