<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight"></app-nav>
        <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper" style="display:none;">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <h3 class="text-dark mb-0"> </h3>
                    <header-btns></header-btns>
                </div>
                <hr>
                <div class="row">

                    <div class="col-xl-3 col-md-4 mb-2 btn" v-for="table in tables" :key="table.table_num">
                        <div class="col-sm-12 rounded text-light" dir="rtl" v-bind:class="{ 'bg-success': table.state == 0, 'bg-primary': table.state == 1, 'bg-danger': table.state == 2 }">
                            <div class="p-2"> 
                                <span class="float-left"> {{ table.position }} </span>
                                <span class="float-right" v-bind:class="{ 'text-danger': table.type == 'VIP' }"> {{ table.type }} </span>
                            </div>
                            <br>
                            <div class="text-center">
                                <img src="../assets/img/dinner.png" style="width: 128px;height: 128px;">
                                <h2> {{ table.table_num }} </h2>
                            </div>
                            <small class="btn btn-primary btn-block mb-2" v-if="table.state == 0" v-on:click="pre_order(table.table_num)">   </small> 
                            <small class="btn btn-danger btn-block mb-2" v-if="table.state == 1" v-on:click="set_null(table.table_num)">   </small> 
                            <div class="alert alert-danger text-center pt-1 pb-1" v-if="table.state == 2">  </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import io from "socket.io-client";
    import showError from "../showError.js";
    import auth from "../auth.js";

    axios.defaults.withCredentials = true;

    const socket = io("http://localhost:3000");
    
    export default {
        name: "tables",
        data(){
            return {
                tables: []
            }
        },
        beforeCreate(){
            auth("tables");
        },
        created(){
            axios.post("http://localhost:3000/tables/getData").then(({data}) => {
                this.tables = data;
            }).catch((err) => {
                showError(err);
            });

        }, 
        mounted(){
            socket.on("preOrdered", (num) => {
                let index = this.tables.findIndex(obj => obj.table_num == num);
                this.tables[index].state = "1";
            });
            socket.on("settedNull", (num) => {
                let index = this.tables.findIndex(obj => obj.table_num == num);
                this.tables[index].state = "0";
            });
            socket.on("taked", (num) => {
                let index = this.tables.findIndex(obj => obj.table_num == num);
                this.tables[index].state = "2";
            });
        },
        methods: {
            pre_order: function(table_num){
                axios.patch("http://192.168.1.5:3000/tables/preOrder/" + table_num).catch((err) => {
                    showError(err);
                });
            },
            set_null: function(table_num){
                axios.patch("http://192.168.1.5:3000/tables/setNull/" + table_num).catch((err) => {
                    showError(err);
                });
            }
        }
    }
</script>