<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;"  class="animate__animated animate__fadeInRight"></app-nav>
        <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper" style="display:none;" >
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <h3 class="text-dark mb-0"> </h3>
                    <header-btns></header-btns>
                </div>
                <hr>
                <div class="row mb-2">
                    <div class="col-xl-3 col-md-4"> 
                        <span class="btn btn-success" data-toggle="modal" data-target="#mdl_expense">    &nbsp; <i class="fas fa-plus"></i> </span> 
                        <span class="btn btn-primary mr-2" v-on:click="refresh()"> Refresh</span> 
                    </div>
                    <div class="col-xl-9 col-md-4"> 
                        <div class="float-left">
                            <input type="text" class="form-control text-center" dir="ltr" placeholder="...     " v-on:keyup.enter="search()" v-model="search_value">
                        </div> 
                    </div>
                </div>
                <hr>
                <div class="table-responsive">
                    <table class="table table-bordered table-striped text-center text-dark">
                        <thead>
                            <th class="text-center" style="width: 5%;"></th>
                            <th class="text-center" style="width: 22%;"> </th>
                            <th class="text-center" style="width: 8%;"></th>
                            <th class="text-center" style="width: 22%;"></th>
                            <th class="text-center" style="width: 11%;"></th>
                            <th class="text-center" style="width: 10%;"></th>
                            <th class="text-center" style="width: 5%;"></th>
                            <th class="text-center" style="width: 5%;"></th>
                        </thead>
                        <tbody>
                            <tr v-for="expense in expenses" :key="expense.expense_id">
                                <td> {{ expense.expense_id }} </td>
                                <td> {{ expense.expense_type }} </td>
                                <td> {{ expense.price }} </td>
                                <td> {{ expense.note }} </td>
                                <td> {{ expense.username }} </td>
                                <td> {{ expense.expense_date.slice(0,10) }} </td>
                                <td> <span class="btn btn-primary btn-block" v-on:click="get_single_expense(expense.expense_id)" data-toggle="modal" data-target="#mdl_expense"> <i class="fas fa-edit"></i> </span> </td>
                                <td> <span class="btn btn-danger btn-block" v-on:click="del_expense(expense.expense_id)"> <i class="fas fa-trash"></i> </span> </td>
                            </tr>
                        </tbody>
                    </table>
                    <div id="pagination-btns" class="p-1 m-1" dir="ltr"> 
                        <button class="btn btn-primary m-1" v-on:click="previous_pagination_btns()">  </button>
                        <button class="btn btn-outline-primary m-1 pagination-btns"
                                style="display:none;"
                                v-for="num in Math.ceil(no_of_expenses / 10)" :key="num"
                                v-bind:class="'pg-group-' + Math.ceil(num / 5)"
                                v-on:click="get_limited_data(num)"> {{ num }} </button>
                        <button class="btn btn-primary m-1" v-on:click="next_pagination_btns()">  </button>
                    </div>
                </div>
                <div class="modal" id="mdl_expense">
                    <div class="modal-dialog text-dark">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="mt-2"> </h5>
                                <button class="btn btn-danger rounded-circle" v-on:click="clear_expense_inputs()" data-dismiss="modal"> &times; </button>
                            </div>
                            <div class="modal-body">
                                <div class="row mb-2" v-if="single_expense.expense_id != null">
                                    <label class="col-sm-3 mt-2"> : </label>
                                    <input type="number" readonly class="form-control col-sm-8" v-model="single_expense.expense_id">
                                </div>
                                <div class="row mb-2">
                                    <label class="col-sm-3 mt-2"> : </label>
                                    <input type="text" min="0" required class="form-control col-sm-8" placeholder="    ..." v-model="single_expense.expense_type">
                                </div>
                                <div class="row mb-2">
                                    <label class="col-sm-3 mt-2">  : </label>
                                    <input type="number" min="0" required class="form-control col-sm-8" placeholder="    ..." v-model="single_expense.price">
                                </div>
                                <div class="row mb-2">
                                    <label class="col-sm-3 mt-2">  : </label>
                                    <textarea class="form-control col-sm-8" rows="3" v-model="single_expense.note"></textarea>
                                </div>
                                <hr>
                                <div class="row mb-2">
                                    <div class="mb-2" v-bind:class="{ 'col-sm-4': single_expense.expense_id != null, 'col-sm-12': single_expense.expense_id == null }"> <span class="btn btn-success btn-block" v-on:click="add_expense()">   &nbsp; <i class="fas fa-plus"></i> </span> </div>
                                    <div class="col-sm-4 mb-2" v-if="single_expense.expense_id != null"> <span class="btn btn-warning btn-block" v-on:click="update_expense()">  &nbsp; <i class="fas fa-edit"></i> </span> </div>
                                    <div class="col-sm-4 mb-2" v-if="single_expense.expense_id != null"> <span class="btn btn-danger btn-block" v-on:click="del_expense(single_expense.expense_id)">   &nbsp; <i class="fas fa-trash"></i> </span> </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import showError from '../showError';
    import auth from "../auth.js";

    axios.defaults.withCredentials = true;
    
    const nullValues = [null, ""];
    export default {
        name:"expense",
        data(){
            return {
                max_pagination_btns: 0,
                pagination_btns: 1,
                expenses: [],
                no_of_expenses: null,
                single_expense: {
                    expense_id: null,
                    expense_type: null,
                    price: 0,
                    note: null
                },
                user: {
                    user_id: null,
                    username: ""
                },
                search_value: ""
            }
        },
        beforeCreate(){
            auth("expense");
        },
        created(){
            this.get_limited_data(1);
            axios.post("http://localhost:3000/getUser").then(({data}) => {
                this.user.user_id = data.user_id;
                this.user.username = data.username;
            }).catch((err) => {
                showError(err);
            });
        },
        mounted(){
            setTimeout(() => {
                var pg_group1 = document.getElementsByClassName("pg-group-1");
                for (let index = 0; index < pg_group1.length; index++) {
                    pg_group1[index].style.display = 'inline';
                }
                this.max_pagination_btns = Number((document.getElementsByClassName('pagination-btns')[document.getElementsByClassName('pagination-btns').length - 1 ].classList[4]).split("-")[2]);
            }, 450);
        },
        methods: {
            next_pagination_btns: function(){
                if(this.pagination_btns < this.max_pagination_btns){
                    var btns_to_hide = document.getElementsByClassName("pg-group-" + this.pagination_btns);
                    for (let index = 0; index < btns_to_hide.length; index++) {
                        btns_to_hide[index].style.display = "none";
                    }
                    this.pagination_btns += 1;
                    var btns_to_show = document.getElementsByClassName("pg-group-" + this.pagination_btns);
                    for (let index = 0; index < btns_to_show.length; index++) {
                        btns_to_show[index].style.display = "inline";
                    }
                } 
            },
            previous_pagination_btns: function(){
                if(this.pagination_btns > 1){
                    var btns_to_hide = document.getElementsByClassName("pg-group-" + this.pagination_btns);
                    for (let index = 0; index < btns_to_hide.length; index++) {
                        btns_to_hide[index].style.display = "none";
                    }
                    this.pagination_btns -= 1;
                    var btns_to_show = document.getElementsByClassName("pg-group-" + this.pagination_btns);
                    for (let index = 0; index < btns_to_show.length; index++) {
                        btns_to_show[index].style.display = "inline";
                    }
                } 
            },
            get_limited_data: function(paginate_number){
                var offset = 0;
                if(paginate_number != 1){
                    offset = (paginate_number - 1)  * 10 ;
                }
                axios.post("http://localhost:3000/expense/getData", {
                    offset
                }).then(({data}) => {
                    this.expenses = data.expenses;
                    this.no_of_expenses = data.no_of_expenses;
                });
            },
            search: function(){
                if(!nullValues.includes(this.search_value)){
                    axios.post("http://localhost:3000/expense/search",{
                        search_value: this.search_value
                    }).then(({data}) => {
                        this.expenses = data;
                        document.getElementById("pagination-btns").style.display = "none";
                    });
                } else {
                    this.$swal("", "   ", "warning");
                }
            },
            refresh: function(){
                this.get_limited_data(1); 
                this.search_value = "";
                document.getElementById("pagination-btns").style.display = "block";
            },
            clear_expense_inputs: function(){
                this.single_expense.expense_id = null;
                this.single_expense.expense_type = null;
                this.single_expense.price = 0;
                this.single_expense.note = null;
            },
            get_single_expense: function(expense_id){
                const [expense] = this.expenses.filter(obj => obj.expense_id === expense_id);
                this.single_expense.expense_id = expense.expense_id;
                this.single_expense.expense_type = expense.expense_type;
                this.single_expense.price = expense.price;
                this.single_expense.note = expense.note;
            },
            add_expense: function(){
                if(!nullValues.includes(this.single_expense.expense_type) && this.single_expense.price >= 0 ){
                    axios.post("http://localhost:3000/expense/addExpense", {
                        expense_type: this.single_expense.expense_type,
                        price: this.single_expense.price,
                        note: this.single_expense.note,
                        user_id: this.user.user_id
                    }).then(({data}) => {
                        this.get_limited_data(1);
                        this.expenses.unshift({
                            expense_id: data.expense_id,
                            expense_type: this.single_expense.expense_type,
                            price: this.single_expense.price,
                            note: this.single_expense.note,
                            expense_date: new Date().toISOString().split("T")[0],
                            username: this.user.username
                        });
                        this.get_single_expense(data.expense_id);
                    }).catch((err) => {
                        showError(err);
                    });
                } else {
                    alert("     ");
                }
            },
            update_expense: function(){
                if(!nullValues.includes(this.single_expense.expense_id)){
                    if(!nullValues.includes(this.single_expense.expense_type) && this.single_expense.price >= 0 ){
                        axios.patch("http://localhost:3000/expense/updateExpense/" + this.single_expense.expense_id, {
                            expense_type: this.single_expense.expense_type,
                            price: this.single_expense.price,
                            note: this.single_expense.note
                        }).then(() => {
                            let index = this.expenses.findIndex(obj => obj.expense_id === this.single_expense.expense_id);
                            this.expenses[index].expense_type = this.single_expense.expense_type;
                            this.expenses[index].price = this.single_expense.price;
                            this.expenses[index].note = this.single_expense.note;
                            alert("  ");
                        }).catch((err) => {
                            showError(err);
                        });
                    } else {
                        alert("     ");
                    }
                } else {
                    alert("      ");
                }
            },
            del_expense: function(expense_id){
                if(!nullValues.includes(expense_id)){
                    var c = confirm("     ");
                    if(c){
                        axios.delete("http://localhost:3000/expense/deleteExpense/" + expense_id).then(()=>{
                            this.expenses = this.expenses.filter(obj => obj.expense_id != expense_id);
                        }).catch((err) => {
                            showError(err);
                        });
                    }
                } else {
                    alert("      ");
                }
            }
        }
    }
</script>